import Removecart from "./Removecart";
import React from "react";
import { Link } from "react-router-dom";
import { useState, useEffect } from "react";
import axios from "axios";
import Ordersummery from "./Ordersummery";
import Order from "./Order";

function Orderlist() {
  const [lists, setLists] = useState([]);
  const [listss, setListss] = useState([]);
  const [listsss, setListsss] = useState([]);
  const [data, setData] = useState();

  useEffect(() => {
    getHomeData();
  }, []);

  const calcPrice = (lists) => {
    return lists.reduce((accumulator, object) => {
      return Number(accumulator) + Number(object.total_price);
    }, 0);
  };

  const calcPrice2 = (lists) => {
    return lists.reduce((accumulator, object) => {
      return Number(accumulator) + Number(object.productId.refund_pollicy);
    }, 0);
  };

  const getHomeData = async () => {
    const userid = localStorage.getItem("_id");

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };
    const data = JSON.stringify({
      userId: userid,
    });

    try {
      const res = await axios.post(
        "http://68.178.168.117:3019/website/order_summery_list",
        data,
        options
      );
      setLists(res.data.data);
    } catch (err) {
      console.error(err);
    }
  };

  const getHomeData2 = async () => {
    const userid = localStorage.getItem("_id");
    const shipingidd = localStorage.getItem("shipingid");

    const storedProductType = localStorage.getItem("productType");

    let priceToSend;

    if (lists.some((list) => list.productId.product_type === "Rent")) {
      const RentPrice =
        calcPrice(lists) +
        calcPrice2(lists) +
        parseInt(listsss[0]?.d_charge) +
        parseInt(((calcPrice(lists) * listss[0]?.gst) / 100).toFixed(0));

      priceToSend = RentPrice;
    } else {
      const Buyprice = Number(
        calcPrice(lists) +
          parseInt(listsss[0]?.d_charge) +
          parseInt(((calcPrice(lists) * listss[0]?.gst) / 100).toFixed(0))
      );

      priceToSend = Buyprice;
    }

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = {
      userId: userid,
      ShippingAddressId: shipingidd,
      t_price: priceToSend,
      types: storedProductType,
    };

    try {
      const response = await axios.post(
        "http://68.178.168.117:3019/website/order",
        data,
        options
      );
      setLists(response.data.data);
      console.log("asdorder detailsss",response.data.data)
    } catch (err) {
      console.error(err);
    }
  };

  useEffect(() => {
    getHomeData5();
  }, []);
  const getHomeData5 = async () => {
    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };
    const data = JSON.stringify({});

    try {
      const res = await axios.get(
        "http://68.178.168.117:3019/website/gst_list",
        data,
        options
      );
      setListss(res.data.data);
    } catch (err) {
      console.error(err);
    }
  };

  useEffect(() => {
    getHomeData6();
  }, []);
  const getHomeData6 = async () => {
    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };
    const data = JSON.stringify({});

    try {
      const res = await axios.get(
        "http://68.178.168.117:3019/website/delivery_list",
        data,
        options
      );
      setListsss(res.data.data);
    } catch (err) {
      console.error(err);
    }
  };

  const Buyprice = Number(
    calcPrice(lists) +
      parseInt(listsss[0]?.d_charge) +
      parseInt(((calcPrice(lists) * listss[0]?.gst) / 100).toFixed(0))
  );

  const RentPrice =
    calcPrice(lists) +
    calcPrice2(lists) +
    parseInt(listsss[0]?.d_charge) +
    parseInt(((calcPrice(lists) * listss[0]?.gst) / 100).toFixed(0));

  // useEffect(() => {

  //   const storedProductType = localStorage.getItem("productType");
  //   if (storedProductType) {

  //     console.log("Retrieved product_type:", storedProductType);
  //   }
  // }, []);

  // useEffect(() => {
  //   localStorage.setItem(
  //     "productType",
  //     lists.map((list) => list.productId.product_type).join(",")
  //   );
  // }, [lists]);


  useEffect(() => {
    if (lists.length > 0) {
      const productTypes = lists
        .map((list) => list.productId.product_type) // Extract product_type from each list item
        .filter((value, index, self) => self.indexOf(value) === index); // Remove duplicates
  
      localStorage.setItem("productType", productTypes.join(","));
    }
  }, [lists]);

  


  return (
    <div className="col-lg-5 col-md-6">
      <aside class="checkout__sidebar sidebar border-radius-10">
        <h2 class="checkout__order--summary__title text-center mb-15">
          Your Order Summary
        </h2>
        <div class="cart__table checkout__product--table">
          <table class="cart__table--inner">
            <tbody class="cart__table--body">
              {lists.map((list) => {
                return (
                  <tr class="cart__table--body__items">
                    <td class="cart__table--body__list">
                      <div class="product__image two  d-flex align-items-center">
                        <div class="product__thumbnail border-radius-5">
                          <a class="display-block" href="product-details.html">
                            <img
                              class="display-block border-radius-5"
                              src={
                                "http://68.178.168.117:3019/uploads/" +
                                list.productId.images[0]
                              }
                              alt="cart-product"
                            />
                          </a>
                          <span class="product__thumbnail--quantity">
                            {list.qty}
                          </span>
                        </div>
                        <div class="product__description">
                          <h4 class="product__description--name">
                            <a href="product-details.html">
                              {list.productId.product_name}
                            </a>
                          </h4>
                          <span class="product__description--name">
                            Color: {list.color} {list.productId.product_type}
                          </span>
                        </div>
                      </div>
                    </td>
                    <td class="cart__table--body__list">
                      <span class="cart__price">{list.total_price}</span>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
        <div class="checkout__total">
          <table class="checkout__total--table">
            <tfoot class="checkout__total--footer">
              <tr class="checkout__total--footer__items">
                <td class="checkout__total--footer__title checkout__total--footer__list text-left">
                  Price
                </td>
                <td class="checkout__total--footer__amount checkout__total--footer__list text-right">
                  {calcPrice(lists)}
                </td>
              </tr>

              <tr class="checkout__total--footer__items">
                <td class="checkout__total--footer__title checkout__total--footer__list text-left">
                  Delivery Charge
                </td>
                {listsss.map((list) => {
                  return (
                    <td class="checkout__total--footer__amount checkout__total--footer__list text-right">
                      {list.d_charge}
                    </td>
                  );
                })}
              </tr>
              <tr class="checkout__total--footer__items">
                <td class="checkout__total--footer__title checkout__total--footer__list text-left">
                  Gst
                </td>
                {listss.map((list) => {
                  return (
                    <td class="checkout__total--footer__amount checkout__total--footer__list text-right">
                      {((calcPrice(lists) * list.gst) / 100).toFixed(0)}
                    </td>
                  );
                })}
              </tr>

              {lists.some((list) => list.productId.product_type === "Rent") && (
                <tr class="checkout__total--footer__items">
                  <td class="checkout__total--footer__title checkout__total--footer__list text-left">
                    Security Deposit
                  </td>
                  <td class="checkout__total--footer__amount checkout__total--footer__list text-right">
                    {calcPrice2(lists)}
                  </td>
                </tr>
              )}

              <tr class="checkout__total--footer__items">
                <td class="checkout__total--footer__title checkout__total--footer__list text-left">
                  Total
                </td>
                <td class="checkout__total--footer__amount checkout__total--footer__list text-right">
                  {" "}
                  {lists.some((list) => list.productId.product_type === "Rent")
                    ? RentPrice
                    : Buyprice}
                </td>
              </tr>
            </tfoot>
          </table>
        </div>

        <Link to="/myaccount">
          <button
            onClick={getHomeData2}
            class="checkout__now--btn primary__btn"
            type="submit"
          >
            Checkout Now
          </button>
        </Link>
      </aside>
    </div>
  );
}

export default Orderlist;
