import { Link } from "react-router-dom";
import React from "react";
import { useState, useEffect } from "react";
import axios from "axios";
import Header from "./Header";
import { useNavigate } from "react-router-dom";

function Allrequests(props) {
  const [lists, setLists] = useState([]);

  const navigate = useNavigate();

  const handleSelectChange = (event) => {
    const selectedOption = event.target.value;
    if (selectedOption === "transaction") {
      navigate("/transaction");
    } else if (selectedOption === "invoices") {
      navigate("/invoices");
    } else if (selectedOption === "allrequests") {
      navigate("/allrequests");
    } else if (selectedOption === "activeitem") {
      navigate("/activeitem");
    } else if (selectedOption === "closeitem") {
      navigate("/closeitem");
    } else {
      // Handle other options if needed
    }
  };

  //  useEffect(() => {
  //    getHomeData();
  //  }, []);
  //  const orderdetails = async () => {
  //    const options = {
  //      headers: {
  //        "content-type": "application/json; charset=utf-8",
  //        "Access-Control-Allow-Origin": "*",
  //      },
  //    };
  //    const data = JSON.stringify({});

  //    await axios
  //      .post("http://68.178.168.117:3019/website/order_details", data, options)
  //      .then((res) => {
  //        setLists(res.data.data);

  //      })
  //      .catch((err) => {});
  //  };

  useEffect(() => {
    getHomeData2();
  }, []);

  const getHomeData2 = async () => {
    const userid = localStorage.getItem("_id");

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };
    const data = JSON.stringify({
      userId: userid,
    });

    try {
      const res = await axios.post(
        "http://68.178.168.117:3019/website/order_history",
        data,
        options
      );
      setLists(res.data.data);
      getHomeData2();
      //console.log("responseasdsd", res.data.data);
    } catch (err) {
      console.error(err);
    }
  };

  return (
    <div>
      <Header />
      <main class="main__content_wrapper">
        <section class="my__account--section section--padding">
          <div class="container">
            <span class="account__welcome--text">
              Hello, {localStorage.getItem("name")} welcome to your dashboard!
            </span>
            <br />
            <Link to="/home" class="buying">
              <button class="choiceus__info--btn primary__btn btn__style3">
                Starting Buying
              </button>
            </Link>
            <p class="choiceus__info--btn primary__btn btn__style3">
              {" "}
              Checkout the offers to save more on your new lifestyle.
            </p>

            <div class="my__account--section__inner border-radius-10 d-flex">
              <div class="account__left--sidebar">
                <h3 class="account__content--title mb-20">My Profile</h3>
                <ul class="account__menu">
                  <li class="account__menu--list active">
                    <Link to="/myaccount">Dashboard</Link>
                  </li>
                  <br />

                  <li>
                    <select
                      name="forma"
                      onChange={handleSelectChange}
                      style={{ height: "41px;width: 154px;" }}
                    >
                      <option value="/mysubscription">My Subscription</option>
                      <option value="transaction">Transactions</option>
                      <option value="invoices">Invoices</option>
                      <option value="allrequests">All Requests</option>
                      <option value="activeitem">Active Items</option>
                      <option value="closeitem">Closed Item</option>
                    </select>
                  </li>
                  <br />

                  <li class="account__menu--list">
                    <Link to="/profile">Profile</Link>
                  </li>
                  <br />

                  <li class="account__menu--list">
                    <Link to="/referral">Referral</Link>
                  </li>
                  <br />

                  <li class="account__menu--list">
                    <Link to="/wishlist">Wishlist</Link>
                  </li>
                  <br />
                  <li class="account__menu--list">
                    <Link to="/Addresslist">Address</Link>
                  </li>

                  <br />
                  <li class="account__menu--list">
                    <Link to="/myaccount2">Setting</Link>
                  </li>
                  <br />
                  {/* <li class="account__menu--list"><Link to="/login">Log Out</Link></li> */}
                  <br />
                  <br />
                  <button class="account__login--btn primary__btn">
                    Need-Help
                  </button>
                </ul>
              </div>
              <div class="account__wrapper">
                <div class="account__content">
                  <h3 class="account__content--title mb-20">Orders History</h3>
                  <div class="account__table--area">
                    <table class="account__table">
                      <thead class="account__table--header">
                        <tr class="account__table--header__child">
                          <th class="account__table--header__child--items">
                            Order-Id
                          </th>

                          <th class="account__table--header__child--items">
                            Payment Mode
                          </th>
                          <th class="account__table--header__child--items">
                            Status
                          </th>
                          <th class="account__table--header__child--items">
                            Total
                          </th>
                        </tr>
                      </thead>
                      {lists.map((list) => {
                        return (
                          <tbody class="account__table--body ">
                            <tr class="account__table--body__child">
                              <td class="account__table--body__child--items">
                                <button  className="account__login--btn primary__btn">
                                  <Link onClick={() =>
                                      localStorage.setItem(
                                        "orderdetailsid",
                                        list._id
                                      )
                                    } to="/orderdetails"> {list._id}</Link>
                                </button>
                              </td>

                              <td class="account__table--body__child--items">
                                {list.paymentMode}
                              </td>
                              <td class="account__table--body__child--items">
                                {list.status}
                              </td>
                              <td class="account__table--body__child--items">
                                {list.t_price}
                              </td>
                            </tr>
                          </tbody>
                        );
                      })}
                    </table>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>
      </main>
    </div>
  );
}

export default Allrequests;
