import Footerone from "./Footerone";
import { useState, useEffect } from "react";
import React from "react";
import axios from "axios";
import Headerone from "./Headerone";
import { Link } from "react-router-dom";
import Header from "./Header";
import { useNavigate } from "react-router-dom";

function Addresslist() {
  const [listss, setListss] = useState([]);
  const [lists, setLists] = useState([]);
  const [data, setData] = useState();
  const [output, setOutput] = useState();
  const [address, setAddress] = useState();
  const [pincode, setPincode] = useState();
  const [city, setCity] = useState();

  const navigate = useNavigate();

  const handleSelectChange = (event) => {
    const selectedOption = event.target.value;
    if (selectedOption === "transaction") {
      navigate("/transaction");
    } else if (selectedOption === "invoices") {
      navigate("/invoices");
    } else if (selectedOption === "allrequests") {
      navigate("/allrequests");
    } else if (selectedOption === "activeitem") {
      navigate("/activeitem");
    } else if (selectedOption === "closeitem") {
      navigate("/closeitem");
    } else {
      // Handle other options if needed
    }
  };
  const handleSubmit = (evt) => {
    evt.preventDefault();
    let userDetails = { address: address, pincode: pincode, city: city };
    const userid = localStorage.getItem("_id");
    const addresss = address;
    const pincodee = pincode;
    const cityy = city;

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };
    const data = JSON.stringify({
      userId: userid,
      address: addresss,
      pincode: pincodee,
      city: cityy,
    });

    axios
      .post("http://68.178.168.117:3019/website/add_address", data, options)
      .then((response) => {
        
        setOutput(response.data.result);
        setAddress("");
        setPincode("");
        setCity("");
      })
      .catch((err) => {
        console.log(err);
      });
  };

  useEffect(() => {
    addreslist();
  }, []);

  const addreslist = async () => {
    const userid = localStorage.getItem("_id");

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };
    const data = JSON.stringify({
      userId: userid,
    });

    try {
      const res = await axios.post(
        "http://68.178.168.117:3019/website/address_list",
        data,
        options
      );
      setListss(res.data.data);
      addreslist();
    } catch (err) {
      console.error(err);
    }
  };

  const deleteadrress = (item) => {
    const userid = localStorage.getItem("_id");
    const shipingidid = localStorage.getItem("shipingid");
    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = JSON.stringify({
      userId: userid,
      addressId: shipingidid,
    });

    axios
      .post("http://68.178.168.117:3019/website/address_delete", data, options)
      .then((res) => {
        deleteadrress();
      })

      .catch((err) => {
        console.error(err);
      });
  };

  return (
    <div>
      <Header />
      <main class="main__content_wrapper">
        <section class="my__account--section section--padding">
          <div class="container">
            <span class="account__welcome--text">
              Hello, {localStorage.getItem("name")} welcome to your dashboard!
            </span>
            <br />
            <Link to="/home" class="buying">
              <button class="choiceus__info--btn primary__btn btn__style3">
                Starting Buying
              </button>
            </Link>
            <p class="choiceus__info--btn primary__btn btn__style3">
              {" "}
              Checkout the offers to save more on your new lifestyle.
            </p>

            <div class="my__account--section__inner border-radius-10 d-flex">
              <div class="account__left--sidebar">
                <h3 class="account__content--title mb-20">My Profile</h3>
                <ul class="account__menu">
                  <li class="account__menu--list active">
                    <Link to="/myaccount">Dashboard</Link>
                  </li>
                  <br />

                  <li>
                    <select
                      name="forma"
                      onChange={handleSelectChange}
                      style={{ height: "41px;width: 154px;" }}
                    >
                      <option value="/mysubscription">My Subscription</option>
                      <option value="transaction">Transactions</option>
                      <option value="invoices">Invoices</option>
                      <option value="allrequests">All Requests</option>
                      <option value="activeitem">Active Items</option>
                      <option value="closeitem">Closed Item</option>
                    </select>
                  </li>
                  <br />

                  <li class="account__menu--list">
                    <Link to="/profile">Profile</Link>
                  </li>
                  <br />

                  <li class="account__menu--list">
                    <Link to="/referral">Referral</Link>
                  </li>
                  <br />

                  <li class="account__menu--list">
                    <Link to="/wishlist">Wishlist</Link>
                  </li>
                  <br />
                  <li class="account__menu--list">
                    <Link to="/Addresslist">Address</Link>
                  </li>

                  <br />
                  <li class="account__menu--list">
                    <Link to="/myaccount2">Setting</Link>
                  </li>
                  <br />
                  {/* <li class="account__menu--list"><Link to="/login">Log Out</Link></li> */}
                  <br />
                  <br />
                  <button class="account__login--btn primary__btn">
                    Need-Help
                  </button>
                </ul>
              </div>

              <div>
                {/* <div class="account__wrapper"> */}
                <div>
                  <div class="account__content ">
                    <h3 class="account__content--title mb-20  ">
                      Address List
                    </h3>
                    <div class="account__table--area ">
                      <table class="account__table  ">
                        <thead class="account__table--header  ">
                          <tr class="account__table--header__child ">
                          <th class="account__table--header__child--items">
                              Delete
                            </th>
                            <th class="account__table--header__child--items">
                              City
                            </th>
                            <th class="account__table--header__child--items">
                              Pincode
                            </th>
                            <th class="account__table--header__child--items">
                              Address
                            </th>
                            
                          </tr>
                        </thead>
                        {listss.map((list) => {
                          return (
                            <tbody class="account__table--body ">
                              <tr class="account__table--body__child">
                               
                              <td class="account__table--body__child--items">
                                  <Link
                                    to="/addresslist"
                                    onClick={() => {
                                      localStorage.setItem(
                                        "shipingid",
                                        list._id
                                      );
                                      deleteadrress();
                                    }}
                                  >
                                    <button><svg fill="currentColor" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 24 24" width="16px" height="16px"><path d="M 4.7070312 3.2929688 L 3.2929688 4.7070312 L 10.585938 12 L 3.2929688 19.292969 L 4.7070312 20.707031 L 12 13.414062 L 19.292969 20.707031 L 20.707031 19.292969 L 13.414062 12 L 20.707031 4.7070312 L 19.292969 3.2929688 L 12 10.585938 L 4.7070312 3.2929688 z"></path></svg></button>
                                  </Link>
                                </td>
                               

                                <td class="account__table--body__child--items">
                                  {list.address}
                                </td>
                                <td class="account__table--body__child--items">
                                  {list.pincode}
                                </td>
                                <td class="account__table--body__child--items">
                                  {list.city}
                                </td>
                              </tr>
                            </tbody>
                          );
                        })}
                      </table>
                    </div>
                  </div>
                </div>

                <div class="checkout__content--step section__shipping--address">
                  <font style={{ color: "blue" }}>{output}</font>
                  <form onSubmit={handleSubmit}>
                    <details>
                      <summary class="checkout__checkbox mb-12">
                        <input
                          class="checkout__checkbox--input"
                          type="checkbox"
                        />
                        <span class="checkout__checkbox--checkmark"></span>
                        <span class="checkout__checkbox--label">
                          Add New Address
                        </span>
                      </summary>
                      <div class="section__shipping--address__content">
                        <div class="row">
                          <div class="col-6 mb-12">
                            <div class="checkout__input--list">
                              <label
                                class="checkout__input--label mb-5"
                                htmlFor="address"
                              >
                                Enter Address
                              </label>
                              <input
                                class="checkout__input--field border-radius-5"
                                type="text"
                                value={address}
                                onChange={(e) => setAddress(e.target.value)}
                                data-constraints="  @Required"
                                id="form-login-number"
                                name="address"
                                placeholder="Enter New Address"
                              />
                            </div>
                          </div>

                          <div class="col-6 mb-12">
                            <div class="checkout__input--list">
                              <label htmlFor="lname">Enter-City</label>
                              <input
                                class="checkout__input--field border-radius-5"
                                type="text"
                                value={city}
                                onChange={(e) => setCity(e.target.value)}
                                data-constraints="@Required"
                                name="city"
                                id="form-login-city"
                                placeholder="Enter-City"
                              />
                            </div>
                          </div>

                          <div class="col-lg-6 mb-12">
                            <div class="checkout__input--list">
                              <label htmlFor="pincode">Pin Code</label>
                              <input
                                class="checkout__input--field border-radius-5"
                                type="number"
                                value={pincode}
                                onChange={(e) => setPincode(e.target.value)}
                                data-constraints="@Required"
                                name="pincode"
                                id="form-login-password"
                                placeholder="Enter Pincode"
                              />
                            </div>
                          </div>
                        </div>
                      </div>
                    </details>
                    <div className="modal-footer">
                      <button
                        type="submit"
                        data-bs-dismiss="modal"
                        className="btn theme-bg-color btn-md fw-bold text-light"
                      >
                        Save changes
                      </button>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </section>
      </main>
      <Footerone/>
    </div>
  );
}

export default Addresslist;
