import Slider from "react-slick";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";

import { Link } from "react-router-dom";
import React, { useState, useEffect } from "react";
import Header from "./Header";
import axios from "axios";
import Footerone from "./Footerone";
import Tabs from "./Tabs";
import Addcart from "./Addcart";

function Productrent() {
  const [color, setColor] = useState("red");
  const [size, setSize] = useState("single");
  const [counter, setCounter] = useState(1);
  const [lists, setLists] = useState();
  const [emiDuration, setEmiDuration] = useState(2); 
  const [selectedTab, setSelectedTab] = useState("prime");


  const handleColorChange = (event) => {
    setColor(event.target.value);
  };

  const handleSizeChange = (event) => {
    setSize(event.target.value);
  };

  const handleCountChange = (event) => {
    setCounter(Number(event.target.value));
  };

  const handleTabSelect = (tab) => {
    setSelectedTab(tab);
  };

  const calculateEmiAmount = () => {
    const totalPrice = lists?.amount;
    let amount;

    if (selectedTab === "prime") {
      if (emiDuration === 2) {
        amount = lists?.p_price2;
      } else if (emiDuration === 3) {
        amount = lists?.p_price3;
      } else if (emiDuration === 6) {
        amount = lists?.p_price6;
      } else if (emiDuration === 9) {
        amount = lists?.p_price9;
      } else if (emiDuration === 12) {
        amount = lists?.p_price12;
      }
    } else if (selectedTab === "value") {
      if (emiDuration === 2) {
        amount = lists?.v_price2;
      } else if (emiDuration === 3) {
        amount = lists?.v_price3;
      } else if (emiDuration === 6) {
        amount = lists?.v_price6;
      } else if (emiDuration === 9) {
        amount = lists?.v_price9;
      } else if (emiDuration === 12) {
        amount = lists?.v_price12;
      }
    }

    const totalPriceWithEmi = amount * counter;

    return Math.floor(totalPriceWithEmi);
  };

  useEffect(() => {
    getHomeData();
  }, []);

  const getHomeData = async () => {
    const id = localStorage.getItem("pro_id");

    const options = {
      headers: {
        "content-type": "application/json; charset=utf-8",
        "Access-Control-Allow-Origin": "*",
      },
    };

    const data = JSON.stringify({
      productId: id,
    });

    try {
      const response = await axios.post(
        "http://68.178.168.117:3019/website/product_details",
        data,
        options
      );

      setLists(response.data.data);
     
  
    } catch (error) {
      console.error(error);
    }
  };

  var settings = {
    className: "",
    dots: true,
    infinite: false,
    slidesToShow: 1,
    slidesToScroll: 1,
    adaptiveHeight: true,
    autoplay: true,
  };

  return (
    <>
      <Header />

      <main className="main__content_wrapper">
        {/* Start breadcrumb section */}
        <section className="breadcrumb__section breadcrumb__bg">
          <div className="container">
            <div className="row row-cols-1">
              <div className="col">
                <div className="breadcrumb__content">
                  <h1 className="breadcrumb__content--title text-white mb-10">
                    Product Details
                  </h1>
                  <ul className="breadcrumb__content--menu d-flex">
                    <li className="breadcrumb__content--menu__items">
                      <Link className="text-white" to="/">
                        Home
                      </Link>
                    </li>
                    <li className="breadcrumb__content--menu__items">
                      <span className="text-white">Product Details</span>
                    </li>
                  </ul>
                </div>
              </div>
            </div>
          </div>
        </section>
        {/* End breadcrumb section */}
        {/* Start product details section */}
        <section className="product__details--section section--padding">
          <div className="container">
            <div className="row row-cols-lg-2 row-cols-lg-2">
              <div className="col">
                <div className="">
                  <div className="">
                    <div className="product__media--preview swiper swiper-initialized swiper-horizontal swiper-pointer-events">
                      <Slider {...settings}>
                        {lists?.images.map((list) => (
                          <img
                            src={
                              "http://68.178.168.117:3019/uploads/" + list
                            }
                            alt="product-media-img"
                          />
                        ))}
                      </Slider>
                    </div>
                  </div>

                  <div className=""></div>
                </div>
              </div>

              <div className="">
                <div className="">
                  <form action="#">
                    <h2 className="">{lists?.product_name}</h2>

                    <div className="product__details--info__price mb-20">
                      {selectedTab === "prime" ? (
                        <span className="current__price">
                          {calculateEmiAmount() || lists?.p_price2}/month
                          <p className="current__price">
                      Rent Price {emiDuration}x{calculateEmiAmount()} = 
                      {calculateEmiAmount() * emiDuration}
                    </p>
                        </span> 
                        
                      )
                       : (
                        <span className="current__price">
                          {calculateEmiAmount() || lists?.v_price2}/month
                        </span>
                      )}
                    </div>
                  
                    

                    <span>
                      Security Price &nbsp;
                      <span className="current__price">
                        {lists?.refund_pollicy}
                      </span>
                    </span>
                    <p>
                      <img
                        style={{ height: "34px", width: "60px" }}
                        src="assets/img/logo/car.jpg"
                      />
                      Get it delivered by {lists?.delevery_time}
                    </p>
                    <div className="product__details--info__rating d-flex align-items-center mb-15">
                      <span
                        className={`product__details--info__rating__item ${
                          selectedTab === "prime" ? "active" : ""
                        }`}
                        onClick={() => handleTabSelect("prime")}
                        style={{
                          background:
                            selectedTab === "prime"
                              ? "#a5a5a5"
                              : "inherit",
                        }}
                      >
                        &nbsp;&nbsp;Prime&nbsp;&nbsp;
                      </span>
                      <span
                        className={`product__details--info__rating__item ${
                          selectedTab === "tenure" ? "active" : ""
                        }`}
                        onClick={() => handleTabSelect("value")}
                        style={{
                          background:
                            selectedTab === "value"
                              ? "#a5a5a5"
                              : "inherit",
                        }}
                      >
                        &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;Value
                      </span>
                    </div>

                    <span className="product__details--info__desc mb-20">
                      Description:-
                    </span>
                    &nbsp;{lists?.description}

                    <div className="product__variant"></div>
                    <div className="product__variant--list mb-20">
                      <fieldset className="variant__input--fieldset"></fieldset>
                    </div>
                    <div className="product__variant--list quantity d-flex align-items-center mb-20">
                      <div className="quantity__box">
                        <button
                          style={{ fontFamily: "monospace" }}
                          type="button"
                          onClick={() => setCounter(counter - 1)}
                        >
                          -
                        </button>
                        {counter}
                        <button
                          style={{ fontFamily: "monospace" }}
                          type="button"
                          onClick={() => setCounter(counter + 1)}
                        >
                          +
                        </button>
                      </div>

                      <button
                        className="primary__btn quickview__cart--btn"
                        onClick={() =>
                          localStorage.setItem("userId", lists._id)
                        }
                      >
                        <Addcart
  emiDurationa={emiDuration}                        
  color={color}
  size={size}
  count={counter}
  price={selectedTab === "prime" ? calculateEmiAmount() * emiDuration : calculateEmiAmount()}
/>

                      </button>
                    </div>
                    <div>
                      Tenures Available:-
                      <select
                        value={emiDuration}
                        onChange={(e) =>
                          setEmiDuration(Number(e.target.value))
                        }
                      >
                        <option value={2}>2 months</option>
                        <option value={3}>3 months</option>
                        <option value={6}>6 months</option>
                        <option value={9}>9 months</option>
                        <option value={12}>12 months</option>
                      </select>
                    </div>
                  </form>
                </div>
              </div>
            </div>
          </div>
        </section>
        {/* End product details section */}
        {/* Start product details tab section */}
        <section className="product__details--tab__section section--padding">
          <Tabs />
        </section>
        {/* End product details tab section */}
      </main>
      <Footerone />
    </>
  );
}

export default Productrent;
